document.addEventListener('DOMContentLoaded', () => {
	'use strict';

	// LOGO
	mainLogo('.logo', "PETR'S PORTFOLIO");

	// BURGER and Menu Marker
	navbar('.burger', 'nav', '#marker', '.nav-links li a', 'active-bar', 'change');

	// Navbar color
	navbarColor();

	// Vivus
	animationByScroll(Vivus, moveBlock, offset);

	// SHOW MORE PROJECTS
	showProjects();

	// Hover teches
	tech();

	// FORM
	forms();
})